.Topbar-about{
    width: 100vw;
    height: 15vh;
    background-color: white;
    color: gray;
    display: flex;
}
.goBack{
    float: left;
    flex: 70%;
    text-align: right;
    line-height: 5rem;
    margin-right: 1rem;
}
.another-nav{
    float: right;
    display: flex;
    flex: 3%;
}
.another-nav-list{
    width: 100%;
    display: flex;
    text-align: right;
}
.another{
    margin: auto;
    list-style-type: none;
    font-size: medium;
    
}
.another a {
    color:  sandybrown;
    text-decoration: none;
}
.goback-button{
    padding: 2px 10px;
    border-radius: 2px;
    background-color: transparent;
    text-shadow: 1px 1px 2px rgb(198, 196, 196);
    border: none;
    color: sandybrown;
}